import {useEffect, lazy, Suspense, useState, useContext} from 'react';
import { Route, Switch, useRouteMatch, useHistory, Redirect, Link } from 'react-router-dom';
import { Icon } from 'react-icons-kit'
import {heartO} from 'react-icons-kit/fa/heartO';
import logo from './image/logo.svg'
import {useDispatch, useSelector} from 'react-redux';
import {getToken} from '../application/selectors/auth';
import { post, fetchData } from '../application/middlewares/index';
import {setCategory,setFilters} from "../application/actions/data";
import { getCategory,getFilters,getUser } from '../application/selectors/auth';
import Lang1 from '../pages/Language/Lang1';
import { LangContext } from "../pages/Language/LangContext";
import Input from "@material-tailwind/react/Input";
import Button from "@material-tailwind/react/Button";
import { yupResolver } from '@hookform/resolvers/yup';
import * as Yup from 'yup';
import { useForm, Controller} from "react-hook-form";
import Loading from '../components/Loading';
import toast from 'react-hot-toast';




function Update({update_id}){
  const { lang, dispatchLang } = useContext(LangContext);
 
    const dispatch = useDispatch();
    const history = useHistory();
    const categories = useSelector(getCategory);
    const user = useSelector(getUser);
    const token = useSelector(getToken);
    const [update, setUpdate] = useState();
    const [forms, setForms] = useState();
    const [loading, setLoading] = useState(true)
    const [filter, setFilter] = useState([]);

    let dil='';
    if (lang=='ru') {dil='ru'} else {dil='tm'}
    const [values, setValues] = useState({})
    // console.log(update_id);
    useEffect(() => {
        dispatch(fetchData({
            url:`user/tm/user-real-estate/${update_id}`,
            token: token,
            action:(response)=>{
                if(response.success){
                    console.log("get_update ",response);
                    setUpdate(response.data.rows);
                    // setValues({"price":response.data?.rows?.price, "area":response.data?.rows?.area})
                    
                    setLoading(false)
                } else {
                    console.log("error_data ",response);
                    }
                }
            }))
    }, [dil]);

    useEffect(()=>{
        if (update?.type_id) {
        dispatch(fetchData({
            url:`tm/specifications-for-type/${update?.type_id}/${update?.category_id}`,
            token:'',
            action:(response)=>{
                if(response.success){
                    console.log("get_form ",response)
                    // console.log(response.data.rows);
                    setForms(response.data.rows);
                } else {
                    console.log("error_data ",response);
                    }
                }
            }))}
    },[update])
    
    const { register ,watch, handleSubmit, formState: { errors }, setError, setValue, getValues} = useForm({
        resolver: yupResolver(schema),
        // defaultValues:{...update}
    });
    setValue("price", update?.price)
    setValue("area", update?.area)
    setValue("description_tm", update?.description_tm)
    setValue("description_ru", update?.description_ru)

    useEffect(()=>{
        for(let i=0; i<forms?.length; i++){
            setFilter(prevState => [...prevState, {"id":forms[i].specification_id, "is_required":forms[i].is_required, "is_multiple":forms[i].is_multiple, values:[]}])
        }
    }, [forms])
    console.log(filter,"-----put")

    function Valuee(value,value1){
        setFilter(filter.map((item)=>{
            if (item.id == value){
                item.values[0]=value1;
            }
            return item;
        }))
        // console.log(value,"----1");
        // console.log(value,"----2");
        // console.log(filter,"---1");
    }

    function Values_more(value,value1){
        setFilter(filter.map((item)=>{
            if (item.id == value){
                if(item.values.includes(value1)){
                    item.values = item.values.filter(item1 =>item1 !== value1)
                }else{
                    item.values.push(value1);
                }
            }
            return item;
        }))
        // console.log(value,"----1");
        // console.log(value,"----2");
    }

    console.log(filter,'-------filter');

    const onSubmit = (data) => {
        console.log(data,"----useForm hook");
        data={...data, "specifications":filter}
        dispatch(post({
            url:`user/tm/update-real-estate/${update?.real_estate_id}`,
            token: token,
            data: data,
            action:(response)=>{
                if(response.success){
                    console.log("update_response ",response)
                    toast.success("Üstünlikli üýtgedildi!");
                    // console.log(response.data.rows);
                    // setForms(response.data.rows);
                } else {
                    console.log("error_data ",response);
                    toast.error("Üstünlikli bolmady!");
                    if (response?.message?.data){
                        let keys = Object?.keys(response?.message?.data?.error);
                        // console.log(keys,"------keys");
                        keys?.map(item => {
                            setError(item, {type:'manual', message:response?.message?.data?.error[item]})
                        })
                        // console.log(keys)
                     }
                    }
                }
            }))

    }
   

    return(
        <>
        {loading ? <Loading type="table"/> : 
        <form onSubmit={handleSubmit(onSubmit)}
         className="profile_main flex flex-col bg-white border rounded-md my-6 p-3">
            <h1>Update</h1>
            <div className="w-full flex justify-between mt-5">
                <div className="title_name1">
                    <p>{Lang1[lang].not15}</p>
                </div>
                <div className="filter_links1">
                <input type="text" {...register("price")}  
                className="p-1 px-2 border rounded-sm w-40 mr-4 focus:outline-none focus:ring-2 focus:ring-blue-600 focus:ring-opacity-50" />
                <span>{Lang1[lang].not16}</span>
                <p className="text-red-500 my-1">{errors.price?.message ? errors.price.message : null}</p>
                </div>        
            </div>
            <div className="w-full flex justify-between mt-5">
                <div className="title_name1">
                    <p>{Lang1[lang].not17}</p>
                </div>
                <div className="filter_links1">
                    <input type="text" {...register("area")} 
                    className="p-1 px-2 border rounded-sm w-40 mr-4 focus:outline-none focus:ring-2 focus:ring-blue-600 focus:ring-opacity-50" />
                    <span>м²</span>
                    <p className="text-red-500 my-1">{errors.area?.message ? errors.area.message : null}</p>

                </div>
            </div>
            <div className="w-full flex justify-between mt-5">
                <div className="title_name1">
                            <p>{Lang1[lang].not18}</p>
                </div>
                <div className="filter_links1">
                    <textarea {...register("description_tm")} 
                    className="border p-1 w-1/2 h-44 text-sm px-2 focus:outline-none focus:ring-2 focus:ring-blue-600 focus:ring-opacity-50"></textarea>
                    {/* <p className="text-red-500 my-1">{errors.descriptions?.message ? errors.descriptions.message : null}</p> */}
                    <p className="text-red-500 my-1">{errors.description_tm?.message ? errors.description_tm.message : null}</p>
                </div>
            </div>
            <div className="w-full flex justify-between mt-5">
                <div className="title_name1">
                    <p>{Lang1[lang].not19}</p>
                </div>
                <div className="filter_links1">
                    <textarea {...register("description_ru")}
                    className="border p-1 w-1/2 h-44 text-sm px-2 focus:outline-none focus:ring-2 focus:ring-blue-600 focus:ring-opacity-50"></textarea>
                    {/* <p className="text-red-500 my-1">{errors.descriptions?.message ? errors.descriptions.message : null}</p> */}
                    <p className="text-red-500 my-1">{errors.description_ru?.message ? errors.description_ru.message : null}</p>
                </div>
            </div>
            {forms?.map((item)=>(
                        <div className="w-full flex justify-between mt-5">
                        <div className="title_name1">
                            <p key={item.id}>{item.name}</p>
                        </div>
                        <div className="filter_links1">
                            <div className="flex flex-wrap">
                                {item.is_multiple ?
                                <div className="flex flex-wrap">
                                {item?.values?.map((item_value)=>(
                                <label 
                                className="radio-toolbar mb-3 border outline-none cursor-pointer rounded-sm p-1 px-3 
                                hover:text-white hover:bg-blue-600 mr-4" for={item_value.value_id} >
                                <input type="checkbox" name={item.name} id={item_value.value_id} className="hidden" onClick={(e) =>{
                                    Values_more(item.specification_id,item_value.value_id);
                                    var selects = document.querySelectorAll(".radio-toolbar");
                                    selects.forEach(function(item) {
                                        // console.log(selects[item]);
                                        if(item.children[0].checked) {
                                            let q= item.classList.add("toolbar");
                                            } else {
                                                let q= item.classList.remove("toolbar");
                                            }
                                      });
                                }  } />
                                <span key={item_value.value_id}>{item_value.absolute_value}</span>
                                </label>
                                ))}
                                </div>
                                :
                                <div className="flex flex-wrap">
                                {item?.values?.map((item_value)=>(
                                <label
                                className="radio-toolbar mb-3 border outline-none cursor-pointer rounded-sm p-1 px-3 
                                hover:text-white hover:bg-blue-600 mr-4" for={item_value.value_id}>
                                <input type="radio" name={item.name} id={item_value.value_id} className="hidden" onClick={() =>{
                                    Valuee(item.specification_id,item_value.value_id);
                                    var selects = document.querySelectorAll(".radio-toolbar");
                                    selects.forEach(function(item) {
                                        // console.log(item.children[0]);
                                        if(item.children[0].checked) {
                                            let q= item.classList.add("toolbar");
                                            } else {
                                                let q= item.classList.remove("toolbar");
                                            }
                                      });
                                }} />
                                <span key={item_value.value_id}>{item_value.absolute_value}</span>
                                </label>
                                ))}
                                </div>
                                }
                                <p className="text-red-500 my-1 ml-2">
                            {errors.specifications?.message ? errors.specifications.message : null}
                        </p>
                            </div>
                        </div>
                    </div>
                    ))}


            <Button
                color="lightBlue"
                buttonType="link"
                size="regular"
                rounded={false}
                block={false}
                iconOnly={false}
                ripple="dark"
                type="submit"
                className="w-40 flex self-center my-4"
            >
                Update
            </Button>
        </form>
    }
    </>
    )
}

const schema =  Yup.object().shape({
    // category_id:Yup.string().required("Hokman bolmaly"),
    // type_id:Yup.string().required("Hokman bolmaly"),
    price:Yup.string().required("Hokman bolmaly"),
    area:Yup.string().required("Hokman bolmaly"),
    // image:Yup.string().required("Hokman bolmaly"),
    // position:Yup.string().required("Hokman yerleshyan yeri bolmaly"),
    description_tm:Yup.string().min(10, "Azyndan 10 simwol").required("Hokman bolmaly"),
    description_ru:Yup.string().min(10, "Azyndan 10 simwol").required("Hokman bolmaly"),
    // location_id:Yup.string().required("Hokman bolmaly"),
  });

export default Update;